import {TextInput} from "./text-input";
import {Validations} from "./../core/validations";
import {ATextInput} from "./abstract/a-text-input";
import {InputAddon} from "./input-addon";

/**
 * @classdesc Campo utilizado na edição de telefone
 * 
 * @example
 * 
 * import {PhoneInput} from "mvcomponents/input";
 * 
 * let phoneInput:PhoneInput = new PhoneInput();
 * phoneInput.setValue(99123412345);
 * phoneInput.getValue(); //(99) 1234-12345
 * 
 * @export
 * @extends {ATextInput}
 * @memberof module:mvcomponents/input
 */
export class PhoneInput extends ATextInput {
	constructor(p_text:string=""){
		super("text", "");
		
		this.addStyleName("InputPhone")
			.setMask("(99) 9999-9999?9")
			.append(new InputAddon()
				.setIcon("glyphicon glyphicon-earphone"));

		this.setSize(3);
		this.validator.add(Validations.maxLength(12));
	}
}
